
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { JwtHelperService } from '@auth0/angular-jwt';
import * as moment from 'moment';

const jwt = new JwtHelperService();

class DecodedToken {
  exp: number;
  username: string;
}

interface TokenResponse {
  token: string;
}

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private uriseg = 'http://testapplication-env.eba-ymzz7z5z.us-east-1.elasticbeanstalk.com/api';
  //private uriseg =  'http://localhost:3000/api';

  private decodedToken;

  constructor(private http: HttpClient) {
    this.decodedToken = JSON.parse(localStorage.getItem('auth_meta')) || new DecodedToken();
  }

  public register(userData: any): Observable<any> {
    const URI = this.uriseg + '/register';
    return this.http.post(URI, userData);
  }

  public login(userData: any): Observable<any> {
    const URI = this.uriseg + '/login';
    //const base = this.http.get(this.uriseg, { headers: { Authorization: `Bearer ${localStorage.getItem('auth_tkn')}` } });
    return this.http.post(URI, userData).pipe(map((token: TokenResponse) => {
       return this.saveToken(token.token);
    }));
  }

  private saveToken(token: any): any {
    this.decodedToken = jwt.decodeToken(token);
    localStorage.setItem('auth_tkn', token);
    localStorage.setItem('auth_meta', JSON.stringify(this.decodedToken));
    return token;
  }

  public logout(): void {
    localStorage.removeItem('auth_tkn');
    localStorage.removeItem('auth_meta');

    this.decodedToken = new DecodedToken();
  }

/*Si l'utilisateur est connect, 
  nous afficherons un lien de dconnexion et un nom d'utilisateur dans la barre de navigation;
  sinon, nous afficherons le lien de connexion et d'enregistrement.*/ 
 public isAuthenticated(): boolean {
  return moment().isBefore(moment.unix(this.decodedToken.exp));
}

/* le nom d'utilisateur connect, Le decodedToken a userId et username */
public getUsername(): string {
  return this.decodedToken.username;
}

public isAdmin(): boolean {
  const localToken = JSON.parse(localStorage.getItem('auth_meta'));
  const isAdmin = localToken.admin;
  return isAdmin;
}

}

