import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class NavBarServiceUser {
  toggled = false;
  menus = [
    {
      title: 'General',
      type: 'header'
    },
    {
      title: 'Dashboard',
      icon: 'fa fa-tachometer-alt',
      active: false,
      type: 'dropdown',
      badge: {
        text: '',
        class: ''
      },
      submenus: [
        {
          title: 'AddUser',
          badge: {
            text: '0',
            class: 'badge-dark'
          },
          href: '/addUser'
        },
        {
          title: 'GetUser',
          badge: {
            text: '0',
            class: 'badge-dark'
          },
          href: '/getUser'
        },
      ]
    },
    {
      title: 'E-commerce',
      icon: 'fa fa-shopping-cart',
      active: false,
      type: 'dropdown',
      badge: {
        text: '',
        class: ''
      },
      submenus: [
        {
          title: 'updateID',
          badge: {
            text: '0 ',
            class: 'badge-dark'
          },
          href: '/updateID'
        },
        {
          title: 'deleteID',
          badge: {
            text: '0',
            class: 'badge-dark'
          },
          href: '/deleteID'
        },
        {
          title: 'Other',
          badge: {
            text: '0',
            class: 'badge-dark'
          },
          href: '/'
        },
      ]
    },
    {
      title: 'Components',
      icon: 'far fa-gem',
      active: false,
      type: 'dropdown',
      submenus: [
        {
          title: 'General',
        },
        {
          title: 'Panels'
        },
        {
          title: 'Tables'
        },
        {
          title: 'Icons'
        },
        {
          title: 'Forms'
        }
      ]
    },
    {
      title: 'Charts',
      icon: 'fa fa-chart-line',
      active: false,
      type: 'dropdown',
      submenus: [
        {
          title: 'Pie chart',
        },
        {
          title: 'Line chart'
        },
        {
          title: 'Bar chart'
        },
        {
          title: 'Histogram'
        }
      ]
    },
    {
      title: 'Maps',
      icon: 'fa fa-globe',
      active: false,
      type: 'dropdown',
      submenus: [
        {
          title: 'Google maps',
        },
        {
          title: 'Open street map'
        }
      ]
    },
    {
      title: 'Extra',
      type: 'header'
    },
    {
      title: 'Documentation',
      icon: 'fa fa-book',
      active: false,
      type: 'simple',
      badge: {
        text: 'Beta',
        class: 'badge-primary'
      },
    },
    {
      title: 'Calendar',
      icon: 'fa fa-calendar',
      active: false,
      type: 'simple'
    },
    {
      title: 'Examples',
      icon: 'fa fa-folder',
      active: false,
      type: 'simple'
    }
  ];
  constructor() { }

  toggle(): void {
    this.toggled = ! this.toggled;
  }

  getSidebarState(): boolean {
    return this.toggled;
  }

  setSidebarState(state: boolean): void {
    this.toggled = state;
  }

  getMenuList() {
    return this.menus;
  }

}
