// General
import { MaterialModule } from './material.module';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { CommonModule } from '@angular/common';

// Modules
import { AuthModule } from './auth/auth.module';

// Components
import { AppComponent } from './app.component';
import { HeaderComponent } from './isNotAuth/header/header.component';
import { ProfileComponent } from './profile/profile.component';
import { PostUserComponent } from './routing/post-user/post-user.component';

// Components Home
import { HomeContentComponent } from './home-content/home-content.component';
import { GetUserComponent } from './routing/get-user/get-user.component';
import { DeleteByIdComponent } from './routing/delete-by-id/delete-by-id.component';
import { UpdateByIdComponent } from './routing/update-by-id/update-by-id.component';

//ngx_bootstrap
import { AlertModule } from 'ngx-bootstrap/alert';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { MultipleCardsComponent } from './multiple-cards/multiple-cards.component';
import { AdminComponent } from './isAuth/admin/admin.component';
import { UserComponent } from './isAuth/user/user.component';
import { NavBarUserComponent } from './isAuth/user/nav-bar-user/nav-bar-user.component';
import { NavBarAdminComponent } from './isAuth/admin/nav-bar-admin/nav-bar-admin.component';


@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    ProfileComponent,
    HomeContentComponent,
    PostUserComponent,
    GetUserComponent,
    DeleteByIdComponent,
    UpdateByIdComponent,
    MultipleCardsComponent,
    AdminComponent,
    UserComponent,
    NavBarUserComponent,
    NavBarAdminComponent,

  ],
  imports: [

    BrowserModule,
    AuthModule,
    BrowserAnimationsModule,
    MaterialModule,
    BsDropdownModule.forRoot(),
    AlertModule.forRoot(),
    CommonModule,
    AppRoutingModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
