import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable} from 'rxjs';
import { map } from 'rxjs/operators';
import { User } from '../models/user';

@Injectable({
  providedIn: 'root'
})
export class RoutingService {

 private userurl = 'http://testapplication-env.eba-ymzz7z5z.us-east-1.elasticbeanstalk.com/api';
 // private userurl =  'http://localhost:3000/api';

  private authorization = { headers: { Authorization: `Bearer ${localStorage.getItem('auth_tkn')}` } }

  constructor(private http: HttpClient) { }


  /**
   * POST d'un User
   */
  addEolien(user: User): Observable<User> {
    console.log('Post du Service', user);
    return this.http.post<User>(this.userurl, user)
      .pipe(
        map((newUser: User) => {
          return newUser;
        })
      );
  }

  /**
  * Get d'un User
  */
 getData(): Observable<User[]> {
    return this.http.get<User[]>(this.userurl + '/users')
      .pipe(
        map((data) => {
          return data;
        }),
      );
  }

}
